.subjectContainer {
    width: 100%;
    height: 100vh;
}

.subjectWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
}

.reservationContainer {
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.reservationWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    margin-inline: auto;
    gap: 2rem;
    background: var(--accent-color);
    padding: 10%;
    border-radius: 2rem;
    margin-top: 4rem;
}

.border {
    width: 0.5rem;
    height: 5%;
    background: var(--dark-color);
    border-radius: 2rem;
    border: none;
}

.inputContainer {
    width: 60%;
}

.totalContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.7rem;
    margin-top: 2rem;
}

.totalContainer h2 {
    font-size: var(--small-fs);
    font-weight: 600;
    color: var(--grey-color);
}

.totalContainer h1 {
    font-size: var(--sec-fs);
    font-weight: 600;
    color: var(--dark-color);
}

.btnContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 3rem;
}

.btnContainer button {
    width: 100%;
    height: 4rem;
    background: var(--dark-color);
    color: var(--light-color);
    font-size: var(--main-fs);
    border-radius: 0.5rem;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

.btnContainer button:hover {
    background: var(--grey-color);
    color: var(--dark-color);
}


/*
    Subject Details
*/

.subjectDetailsContainer {
    width: 60%;
}

.subjectDetailsWrapper {
    width: 80%;
    height: 100%;
    margin-inline: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
}

.subjectTitle h1 {
    font-size: var(--big-fs);
    font-weight: 600;
    color: var(--sec-color);
}


.subjectDescription p {
    font-size: var(--small-fs);
    font-weight: 500;
    color: var(--dark-grey-color);
    line-height: 1.5;
}

.subjectPrice h2 {
    font-size: var(--main-fs);
    font-weight: 600;
    color: var(--dark-grey-color);
}